  display: none;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.contenedorEscenario {
  height: 60rem;
  aspect-ratio: 288/512;
  background-color: blue;
}

.contenedorTuberias {
  height: 1000%;
  display: flex;
  width: auto;
  background-color: blue;
  display: flex;
  flex-direction: column;
  position: absolute;
  gap: 50rem;
  right: -3rem;
  top: 0rem;
}
.contenedorTuberias .tuberiaSuperior, .contenedorTuberias .tuberiaInferior {
  height: 16rem;
  width: 2.6rem;
  background-color: red;
  background-image: url("/build/img/sprites.png");
  background-position: -5.6rem -32.3rem;
  scale: 2.2;
  z-index: 100;
  animation: slideRight 3s linear infinite;
}
.contenedorTuberias .tuberiaSuperior::before, .contenedorTuberias .tuberiaInferior::before {
  content: "";
  height: 14rem;
  width: 2.6rem;
  background-image: url("/build/img/sprites.png");
  background-position: -5.6rem -32.3rem;
  top: 30rem;
  display: flex;
  z-index: 100;
  transform: translateY(-12rem);
}
@keyframes slideRight {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-700%);
  }
}
.contenedorTuberias .tuberiaInferior {
  background-position: -8.4rem -32.3rem;
}
.contenedorTuberias .tuberiaInferior::before {
  transform: translateY(12rem);
}

.salto {
  animation: salto 0.5s ease-in-out linear;
}

.sprites {
  position: absolute;
  display: inline-block;
  image-rendering: pixelated;
  background-repeat: no-repeat;
}

.escenario {
  width: 100%; /* Establece el ancho deseado */
  height: 100%;
  background-repeat: no-repeat;
  background-image: url("/build/img/bgDia.png");
  overflow: hidden;
  background-size: cover;
  position: relative;
}
.escenario .jugador {
  width: 1.8rem;
  height: 1.2rem;
  background-color: red;
  background-image: url("/build/img/sprites.png");
  background-position: -0.3rem -49.1rem;
  left: 20%;
  top: 50%;
  transform: scale(2);
}
@keyframes move {
  to {
    background-position: -8.6rem -49.1rem;
  }
}
.escenario .contenedorPiso {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
}
.escenario .piso {
  height: 5.5rem;
  width: 17rem;
  background-image: url("/build/img/sprites.png");
  background-position: -29.1rem 0rem;
  transform: scale(2);
  bottom: 0;
}

.pantallaPerdiste {
  height: 100%;
  width: 100%;
  background-color: rgba(128, 128, 128, 0.566);
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 101;
}
.pantallaPerdiste h3, .pantallaPerdiste p {
  font-size: 3rem;
}
.pantallaPerdiste h3 {
  font-weight: 600;
}
.pantallaPerdiste .contenedorInterfaz {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.oculto {
  display: none !important;
}

h3 {
  margin: 0;
}

.btnJugarOtraVez {
  cursor: pointer;
  font-size: 3rem;
}